*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Condensed', sans-serif;
}
body{
    background-color: lightgrey;
}
.head-div{
    text-align: center;
    margin-top: 40px;
    margin-bottom: 30px;
    width: 80%;
    padding: 8px;
    margin: 20px auto;

}
.head-div2{
    margin-top: 20px;
    color: red;
    text-align: center;
    background-color: bisque;
    border: 2px solid brown;
    border-radius: 40px;
    padding: 4px;
}

.content{
    width: 70%;
    margin: auto;
    height: auto;
    background-color: lightpink;
    padding: 10px;
    border-radius: 40px;
    box-shadow: 1px 1px 5px 2px darkred;

}
.info{
   
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    margin: auto;
}

.details{
    display: flex;
    justify-content: center;
    align-items: center;
}
.details ul li{
    list-style: none;
    font-size: 1.3rem;
    font-weight: bolder;
    margin: 0 8px;
}
.span{
    margin:auto;
    padding:  0 6px;
    width: 600px;
    
}
.input{
    width: 60%;
    margin: auto 6px;
    padding: 6px;
    border-radius: 40px;
    margin-top: 40px;
    font-size: 1.4rem;
    font-weight: bold;
}
.icon-span{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.plus{
    margin:0 4px;
    padding:6px  6px;
    width: 35px;
    background-color: lightsalmon;
    height: 34px;
    border-radius: 40px;
    border: 2px solid red;
    font-weight: bolder;
    font-size: 1.3rem;
    cursor: pointer;

}
.plus:hover{
    background-color: green;
    color: white;
    border: 2px solid black;
}
.todo-style{
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    
}
.cross{
    margin: auto;
    width: auto;
    background-color: bisque;
    border: 2px solid black;
    padding: 6px;
    border-radius: 40px;
    cursor: pointer;
}
.cross:hover{
    background-color: red;
    color: white;
    border: 2px solid black;
}
.text-list{
    margin: auto;
    width: 200px;
    
}

@media (max-width:160px){
    .head-div2{
        font-size:1.4rem;
    }
    
}
@media (max-width:400px){
    .text-list{
        width: 60%;
        font-size: 1.3rem;
        
    }
    .todo-style{
        width: 50%;
        padding: 0 10px;
    }
}
